import style from './Skills.module.scss'
import {MSkill, Skill} from "./Skill";
import react_logo from '../../Images/icons/Skills_logo/react.svg'
import html_logo from '../../Images/icons/Skills_logo/html5.svg'
import css_logo from '../../Images/icons/Skills_logo/css.svg'
import js_logo from '../../Images/icons/Skills_logo/js.svg'
import redux_logo from '../../Images/icons/Skills_logo/redux.svg'
import typescript_logo from '../../Images/icons/Skills_logo/typescript.png'

import github_logo from '../../Images/icons/Skills_logo/github.gif'
import sass_logo from '../../Images/icons/Skills_logo/sass.svg'



const rightAnimation = {
    hidden: {
        y: 200,
        opacity: 0,
    },
    visible: custom => ({
        y: 0,
        opacity: 1,
        transition: {delay: custom * 0.3}
    })
}
const leftAnimation = {
    hidden: {
        y: -200,
        opacity: 0,
    },
    visible: custom => ({
        y: 0,
        opacity: 1,
        transition: {delay: custom * 0.3}
    })
}
export const Skills =()=> {


    return (
        <div className={style.skills_container}
             initial='hidden'
             whileInView='visible'
             viewport={{once: true}}
            >

            <MSkill title={'HTML'} logo={html_logo} info={'HTML — стандартизированный язык разметки документов для просмотра веб-страниц в браузере.'}/>
            <MSkill title={'CSS'} logo={css_logo} info={'CSS — формальный язык описания внешнего вида документа, написанного с использованием языка разметки.'}/>
            <MSkill title={'JS'} logo={js_logo} info={'JavaScript — мультипарадигменный язык программирования.'}/>
            <MSkill title={'React'} logo={react_logo} info={'React — JavaScript-библиотека для разработки пользовательских интерфейсов.'}/>
            <MSkill title={'Redux'} logo={redux_logo} info={'Redux — библиотека для JavaScript предназначенная для управления состоянием приложения.'}/>
            <MSkill title={'TypeScript'} logo={typescript_logo} info={'TypeScript — язык для разработки веб-приложений, расширяющее возможности JavaScript.'}/>
            <MSkill title={'GitHub'} logo={github_logo} info={'GitHub — крупнейший веб-сервис для хостинга IT-проектов и их совместной разработки.'}/>
            <MSkill title={'Sass'} logo={sass_logo} info={'Sass — это метаязык, предназначенный для увеличения уровня абстракции CSS-кода и упрощения файлов каскадных таблиц стилей.'}/>

        </div>
    )
}